.SettingContainer {
  color: var(--black);
  width: 100%;
  /* height: 100vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px;
}
.SettingWrapper {
  display: flex;
  display: grid;
  /* height: 97%; */
  width: 100%;
  background: var(--glass);
  border-radius: 2rem;
  gap: 16px;
  grid-template-columns: 5rem auto 25rem;
  overflow: hidden;
}
